import React, { useEffect, useState } from 'react'
import axios from 'axios'

const querystring = require('querystring')

const Vote = (props) => {

  const [topics, setTopics] = useState([])

  const url = process.env.REACT_APP_DATABASE_URL

  const fetchTopics = () => axios.get(`${url}/topics`).then((res) => setTopics(res.data)).catch(error => console.log(error))

  useEffect(() => {
    fetchTopics()
    setInterval(() => {
      fetchTopics()
    }, 2000)
  }, [fetchTopics])

  const vote = async (id) => {
    await axios.post(`${url}/vote/${id}`, querystring.stringify({ user: props.user }))
  }

  return (
    <div className='Container'>
      <h1>Äänestä</h1>
      <div className='Topic-list'>
        <h2>Aiheet</h2>
        {topics.length > 0 && topics.map(topic => <div key={topic.name} className='Topic-vote' onClick={() => vote(topic.id)}><span>{topic.name}</span></div>)}
      </div>
    </div>
  )
}

export default Vote
